body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


@media print {
	* { -webkit-print-color-adjust: exact; }
	html { background: none; padding: 0; }
	body, #invoice-wrapper { -webkit-box-shadow: none; box-shadow: none; margin: 0; }
	span:empty { display: none; }
	.add, .cut, .Control { display: none; }
}

@page { margin: 0; }



